import React, { Component } from "react";
import './Form.css'
const validEmailRegex = RegExp(
  /^(([^<>()[\].,;:\s@"]+(\.[^<>()[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/i
);
const validateForm = (errors) => {
  console.log(errors);
  let valid = true;
  //The Object.values() method returns an array of a given object's own enumerable property values,
  Object.values(errors).forEach(
    // if we have an error string set valid to false

    (val) => {
      console.log(val);
      console.log(val.length);
      return val.length > 0 && (valid = false);
    }
  );
  console.log(valid);
  return valid;
};

const error = {
  color: "red",
  fontSize: "1.2rem",
};


class Form extends Component {
  initialState = {
    name: "",
    job: "",
    email: "",
    errors: {
      name: "",
      email: "",
      job: "",
    },
    isError: true,
  };
  state = this.initialState;

  componentWillUpdate(nextProps, nextState) {
    nextState.isError = !(nextState.email && nextState.name && nextState.job);
  }

  handleChange = (event) => {
    const { name, value } = event.target;
    let errors = this.state.errors;
    switch (name) {
      case "name":
        errors.name = value.length < 5 ? "Name must be 5 characters long!" : "";
        break;
      case "email":
        errors.email = validEmailRegex.test(value) ? "" : "Email is not valid!";
        break;
      case "job":
        errors.job = value.length === 0 ? "Please provide job title" : "";
        break;
      default:
        break;
    }
    this.setState({ errors, [name]: value });
  };

  submitForm = (event) => {
    event.preventDefault();

    if (validateForm(this.state.errors)) {
      this.props.handleSubmit(this.state);
      this.setState(this.initialState);
    } else {
      console.error("Invalid Form");
      this.setState({ errorMessage: "Please Fill in the details:" });
    }
  };
  render() {
    const { name, job, email, errors , isError} = this.state;

    return (
      <div>
        <h1>Enter Your Details</h1>
        <form noValidate>
          <label htmlFor="name">Name</label>
          <input
            type="text"
            name="name"
            id="name"
            value={name}
            onChange={this.handleChange}
          />
          {errors.name.length > 0 && <span style={error}>{errors.name}</span>}
          <label htmlFor="job">Job</label>
          <input
            type="text"
            name="job"
            id="job"
            value={job}
            onChange={this.handleChange}
          />
          {errors.job.length > 0 && <span style={error}>{errors.job}</span>}
          <label htmlFor="email">Email</label>
          <input
            type="email"
            name="email"
            id="email"
            value={email}
            onChange={this.handleChange}
          />
          {errors.email.length > 0 && <span style={error}>{errors.email}</span>}
          <div>
            <input type="button" 
            className="button"
            value="Submit" onClick={this.submitForm} disabled={isError} />
          </div>
        </form>
      </div>
    );
  }
}

export default Form;
